<template>
  <div class="music">
    <img v-if="musicObj" :src="musicObj.al.picUrl" alt />
    <div class="imgbox">
      <img src="../assets/needle-ip6.png" alt />
      <div class="m-box">
        <div class="m-router">
          <div class="m-bg" :style="{ 'background-image': `url(${bg})` }"></div>
          <div
            class="m-url"
            :style="{ 'animation-play-state': start ? 'running' : 'paused' }"
            @click="stop"
          >
            <img v-if="musicObj" :src="musicObj.al.picUrl" alt />
          </div>
          <audio
            :src="audioUrl"
            ref="audio"
            @timeupdate="currentTimes"
            @canplay="play"
          ></audio>
        </div>
      </div>
      <p class="btn" v-show="!start">
        <i @click="play">&#xe710;</i>
      </p>
      <div class="songmsg">
        <div class="gename">
          <span>{{ musicObj.name }} -</span>
          <em>&nbsp{{ musicObj.ar[0].name }}</em>
        </div>
        <div class="geci">
          <p>{{ curlyr }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
let bg = require("../assets/disc.png");
export default {
  name: "MusicPlay",
  data() {
    return {
      bg: bg,
      start: false,
      audioUrl: "",
      musicObj: "",
      mylyric: [],
      curlyr: ""
    };
  },
  created() {
    const baseUrl = "https://music.tryto.cn";
    let axios = this.$axios;
    let id = this.$route.query.ids;
    axios.get(baseUrl + `/song/detail?ids=${id}`).then(res => {
      // console.log(res.data.songs[0]);
      this.musicObj = res.data.songs[0];
      this.audioUrl = `https://music.163.com/song/media/outer/url?id=${id}.mp3`;
    });
    axios.get(baseUrl + `/lyric?id=${id}`).then(res => {
      // console.log(res.data.lrc.lyric.split("\n"));
      let lyrArr = res.data.lrc.lyric.split("\n");
      let lyrObj = [];
      for (let i of lyrArr) {
        let t = i.substring(i.indexOf("[") + 1, i.indexOf("]"));
        // console.log(t)
        let time = (t.split(":")[0] * 60 + parseFloat(t.split(":")[1])).toFixed(
          3
        );
        // console.log(time);
        if (time) {
          lyrObj.push({
            t: time,
            ge: i.substring(i.indexOf("]") + 1, i.length)
          });
        }
      }
      // console.log(lyrObj);
      this.mylyric = lyrObj;
      // console.log(this.mylyric)
    });
  },
  methods: {
    play() {
      this.start = true;
      this.$refs.audio.play();
      // console.log(1);
    },
    stop() {
      this.start = false;
      this.$refs.audio.pause();
      // console.log(2);
    },
    currentTimes() {
      let t = this.$refs.audio.currentTime + 0.5;
      // console.log(t)
      for (let i in this.mylyric) {
        if (t >= this.mylyric[i]["t"]) {
          if (this.curlyr != this.mylyric[i]["ge"] && this.mylyric[i]["ge"]) {
            this.curlyr = this.mylyric[i]["ge"];
            // console.log(this.mylyric[i]["ge"])
          }
        }
      }
    }
  }
};
</script>

<style scoped>
@keyframes rotate {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.music {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.music > img {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: -9;
  filter: blur(60px);
}
.imgbox {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
}
.imgbox > img {
  width: 200px;
  position: absolute;
  top: 0;
  left: 300px;
}
.m-box {
  width: 600px;
  height: 600px;
  margin: 160px auto 40px;
}
.m-router {
  width: 100%;
  height: 100%;
}
.m-bg {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.m-url {
  width: 380px;
  height: 380px;
  position: absolute;
  top: 276px;
  left: 186px;
  border-radius: 100%;
  overflow: hidden;
  animation: rotate 15s linear infinite;
  animation-play-state: paused;
}
.btn {
  position: absolute;
  top: 400px;
  left: 320px;
}
.btn i {
  font-size: 100px;
  color: #bbb;
}
.songmsg {
  width: 100%;
  height: 50px;
}
.gename {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 60px;
  box-sizing: border-box;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.gename span {
  color: #fefefe;
  font-size: 36px;
}
.gename em {
  font-size: 30px;
  color: hsla(0, 0%, 100%, 0.6);
}
.geci {
  width: 100%;
  height: 100px;
  margin-top: 50px;
  overflow: hidden;
}
.geci p {
  width: 100%;
  height: 100px;
  line-height: 100px;
  font-size: 36px;
  text-align: center;
  color: #fff;
}
</style>
